import React, { useState } from 'react'
import styled from 'styled-components';

const Container = ({ className }) => {
    const [value, setValue] = useState(50);
    return (
        <div className={className}>
            <h2>Custom Range Slider</h2>
            <div className="range-container">
                <input type="range" value={value} min="0" max="100" onChange={(e) => {
                    setValue(e.target.value);
                }} />
                <label htmlFor="range" style={{ left: `${(2.8 * value - 30)}px` }}>{value}</label>
            </div>
        </div>
    )
}

const CustomRangeSlider = styled(Container)`
    & {
        background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
        font-family: 'Lato', sans-serif;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: calc(100vh - 80px);
        position: relative;
        top: 80px;
        overflow: hidden;
        margin: 0;
    }
    
    h2 {
        position: absolute;
        top: 10px;
    }
    
    .range-container {
        position: relative;
    }
    
    input[type='range'] {
        width: 300px;
        margin: 18px 0;
        -webkit-appearance: none;
    }
    
    input[type='range']:focus {
        outline: none;
    }
    
    input[type='range'] + label {
        background-color: #fff;
        position: absolute;
        top: -25px;
        left: 110px;
        width: 80px;
        padding: 5px 0;
        text-align: center;
        border-radius: 4px;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    }
    
    /* Chrome & Safari */
    input[type='range']::-webkit-slider-runnable-track {
        background: purple;
        border-radius: 4px;
        width: 100%;
        height: 10px;
        cursor: pointer;
    }
    
    input[type='range']::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 24px;
        width: 24px;
        background: #fff;
        border-radius: 50%;
        border: 1px solid purple;
        margin-top: -7px;
        cursor: pointer;
    }
    
    /* Firefox */
    input[type='range']::-moz-range-track {
        background: purple;
        border-radius: 4px;
        width: 100%;
        height: 13px;
        cursor: pointer;
    }
    
    input[type='range']::-moz-range-thumb {
        -webkit-appearance: none;
        height: 24px;
        width: 24px;
        background: #fff;
        border-radius: 50%;
        border: 1px solid purple;
        margin-top: -7px;
        cursor: pointer;
    }
    
    /* IE */
    input[type='range']::-ms-track {
        background: purple;
        border-radius: 4px;
        width: 100%;
        height: 13px;
        cursor: pointer;
    }
    
    input[type='range']::-ms-thumb {
        -webkit-appearance: none;
        height: 24px;
        width: 24px;
        background: #fff;
        border-radius: 50%;
        border: 1px solid purple;
        margin-top: -7px;
        cursor: pointer;
    }
`

export default CustomRangeSlider
